/* Reset & globals */
* { box-sizing: border-box; margin:0; padding:0; }
body { font-family: Arial, sans-serif; line-height:1.6; }
a { text-decoration:none; color:inherit; }

/* Utility button */
.btn { display:inline-block; padding:0.75rem 1.5rem; background:#000; color:#fff; border:none; border-radius:4px; cursor:pointer; }

/* Layout wrappers */
.container { width:90%; max-width:1200px; margin:0 auto; }

/* Navbar */
.navbar { display:flex; justify-content:space-between; align-items:center; padding:1rem 0; }
.nav-links { display:flex; gap:1rem; }

/* Hero */
.hero { background:#000; color:#fff; text-align:center; padding:8rem 1rem 4rem; position:relative; }
.hero img { position:absolute; right:1rem; top:1rem; max-width:300px; opacity:0.3; }

/* Trusted-by logos */
.trusted { display:flex; flex-wrap:wrap; gap:1rem; justify-content:center; margin:2rem 0; }
.trusted img { max-height:40px; opacity:0.7; }

/* Stats */
.stats { display:flex; justify-content:space-around; margin:2rem 0; }
.stats .stat { text-align:center; }
.stats .stat h3 { font-size:2rem; }

/* Services tabs */
.services-tabs { display:flex; flex-direction:column; gap:1rem; }
.service-btns { display:flex; gap:1rem; }
.service-btns button { flex:1; }

/* How it works */
.how { display:grid; grid-template-columns:repeat(auto-fit, minmax(200px,1fr)); gap:1rem; margin:2rem 0; }
.how .step { border:1px solid #ddd; padding:1rem; border-radius:4px; }

/* Testimonials */
.testimonials { position:relative; padding:2rem 0; }
.testimonials .cards { display:flex; gap:1rem; overflow-x:auto; }
.testimonials .card { min-width:250px; border:1px solid #ddd; padding:1rem; border-radius:4px; }

/* Blog preview */
.blog-preview { display:grid; grid-template-columns:repeat(auto-fit, minmax(250px,1fr)); gap:1rem; margin:2rem 0; }

/* Certifications */
.certs { display:flex; flex-wrap:wrap; gap:1rem; justify-content:center; margin:2rem 0; }
.certs img { max-height:50px; }

/* Footer */
.footer { background:#111; color:#ccc; padding:2rem 1rem; }
.footer .footer-links { display:flex; flex-wrap:wrap; gap:1rem; justify-content:center; margin-bottom:1rem; }
